// custom checkbox
*,
*:after,
*::before {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box; }

.ac-custom {
    max-width: 900px;
    margin: 0 auto; }

.ac-custom h2 {
    font-size: 3em;
    font-weight: 300;
    padding: 0 0 0.5em;
    margin: 0 0 30px; }

.ac-custom ul {
    list-style: none;
    padding: 0;
    margin: 0 auto 30px;
    max-width: 800px; }

.ac-custom li {
    margin: 0 auto;
    position: relative; }

.ac-custom label {
    display: inline-block;
    position: relative;
    font-size: 1.5em;
    padding: 8px 0 0 60px;
    // vertical-align: top
    color: rgb(218, 181, 247);
    cursor: pointer;
    -webkit-transition: color 0.3s;
    transition: color 0.3s;
    font-weight: 100; }

.ac-custom input[type="checkbox"],
.ac-custom input[type="radio"],
.ac-custom label::before {
    width: 40px;
    height: 40px;
    top: 50%;
    left: 5px;
    margin-top: -16px;
    position: absolute;
    cursor: pointer; }

.ac-custom input[type="checkbox"],
.ac-custom input[type="radio"] {
    opacity: 0;
    -webkit-appearance: none;
    display: inline-block;
    vertical-align: middle;
    z-index: 100; }

.ac-custom label::before {
    content: '';
    border: 2px solid #fff;
    -webkit-transition: opacity 0.3s;
    transition: opacity 0.3s; }

.ac-custom input[type="checkbox"]:checked + label,
.ac-custom input[type="radio"]:checked + label {
    color: #fff; }

.ac-custom input[type="checkbox"]:checked + label::before,
.ac-custom input[type="radio"]:checked + label::before {
    opacity: 0.8; }

/* General SVG and path styles */

.ac-custom svg {
    position: absolute;
    width: 40px;
    height: 40px;
    top: 50%;
    margin-top: -20px;
    left: 5px;
    pointer-events: none; }

.ac-custom svg path {
    stroke: #fdfcd3;
    stroke-width: 13px;
    stroke-linecap: round;
    stroke-linejoin: round;
    fill: none; }

// placeholders
::-webkit-input-placeholder {
    color: #A894B7; }
::-moz-placeholder {
    color: #A894B7; }
:-moz-placeholder {
    color: #A894B7; }
:-ms-input-placeholder {
    color: #A894B7; }

